import React, { Component } from "react";
import { Button } from 'react-bootstrap';
import history from '../../history';

export default class Retailerpage extends Component {
  render() {
    return (
      <div className="Retailerpage">
        <div className="lander">
          <h1>Welcome Retailer</h1>
          <form>
            <Button variant="btn btn-success" onClick={() => history.push('/Customerpage')}>Go to Customer side</Button>
            <Button variant="btn btn-success" onClick={() => history.push('/')}>Go to Login</Button>
          
          </form>
        </div>
      </div>
    );
  }
}